export const formatDate = ({ dateFromBase, type }) => {
    
    const date = new Date();
    const dt = new Date(dateFromBase);

    const dayMonthDate = ((date.getDate()) + '').padStart(2, "0");
    const dayMonthDt = ((dt.getDate()) + '').padStart(2, "0");

    const monthDate = ((date.getMonth() + 1) + '').padStart(2, "0");
    const monthDt = ((dt.getMonth() + 1) + '').padStart(2, "0");

    const yearDate = date.getFullYear();
    const yearDt = dt.getFullYear();

    const hours = (dt.getHours() + '').padStart(2, "0");
    const minutes = (dt.getMinutes() + '').padStart(2, '0');

    const returnDate = () => {
        switch(type) {
            case 'dialogs': 
                return `${dayMonthDt}.${monthDt}.${yearDt}`;
            case 'messages':
                return `${dayMonthDt}.${monthDt}.${yearDt} - ${hours}:${minutes}`;
            default:
                return `${dayMonthDt}.${monthDt}.${yearDt} - ${hours}:${minutes}`;
                
        }
    };

    if(dayMonthDate !== dayMonthDt) { //–µ—Å–ª–∏ –¥–Ω–∏ –º–µ—Å—è—Ü–∞ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç
        if((dayMonthDate - dayMonthDt) === 1) { // –µ—Å–ª–∏ —É–∂–µ –Ω–∞—Å—Ç—É–ø–∏–ª —Å–ª–µ–¥—É—é—â–∏–π –¥–µ–Ω—å
            if(monthDate === monthDt && yearDate === yearDt) { //–µ—Å–ª–∏ –º–µ—Å—è—Ü–∞ –∏ –≥–æ–¥–∞ —Å–æ–≤–ø–∞–¥–∞—é—Ç
                if(type === 'dialogs') { //–µ—Å–ª–∏ –¥–∏–∞–ª–æ–≥
                    return '–í—á–µ—Ä–∞'; //—Ç–æ –≤–µ—Ä–Ω–∏ "–í—á–µ—Ä–∞"
                } else return `–í—á–µ—Ä–∞ - ${hours}:${minutes}`; //–∏–Ω–∞—á–µ –≤–µ—Ä–Ω–∏ "–≤—á–µ—Ä–∞" –∏ –≤—Ä–µ–º—è
            } else return returnDate(); //–∏–ª–∏ –µ—Å–ª–∏ –º–µ—Å—è—Ü –∏–ª–∏ –≥–æ–¥ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç, —Ç–æ –≤–µ—Ä–Ω–∏ –ø—Ä–æ—Å—Ç–æ –¥–∞—Ç—É
        } else return returnDate();  //–∏–ª–∏ –µ—Å–ª–∏ –Ω–∞—Å—Ç—É–ø–∏–ª —É–∂–µ –±–æ–ª—å—à–µ, —á–µ–º –æ–¥–∏–Ω –¥–µ–Ω—å
    } else { //–Ω–æ –µ—Å–ª–∏ —Å–æ–≤–ø–∞–¥–∞–µ—Ç –¥–µ–Ω—å
        if(monthDate !== monthDt || yearDate !== yearDt) { //–µ—Å–ª–∏ –∏–ª–∏ –º–µ—Å—è—Ü, –∏–ª–∏ –≥–æ–¥ –Ω–µ —Å–æ–≤–ø–∞–¥–∞—é—Ç
            return returnDate();  //–≤–æ–∑–≤—Ä–∞—â–∞–µ–º –¥–∞—Ç—É
        } else { //–µ—Å–ª–∏ —Å–æ–≤–ø–∞–¥–∞–µ—Ç –∞–±—Å–æ–ª—é—Ç–Ω–æ –≤—Å–µ
            return `${hours}:${minutes}`;
        }
    }

};