import React from 'react';

import InputGroup from 'components/templates/InputGroup/InputGroup';

const Settings = ({ token, email, nickname, avatar, changeHandler, updateSettingsThunk, deleteAccThunk }) => {
    return (
        <div className="settings mt-3">
            <ul className="list-group list-group--emptyborder" >
                <li className="list-group-item">
                    <InputGroup 
                        caption={'Email'}
                        value={email ? email : ''}
                        name={'email'}
                        readonly={true} />
                </li>
                <li className="list-group-item">
                    <InputGroup 
                        caption={'–ù–∏–∫–Ω—ç–π–º'}
                        handler={changeHandler}
                        value={nickname ? nickname : ''}
                        name={'nickname'} />
                </li>
                <li className="list-group-item">
                    <InputGroup 
                        caption={'–°—Å—ã–ª–∫–∞ –Ω–∞ –∞–≤–∞—Ç–∞—Ä'}
                        handler={changeHandler}
                        value={avatar ? avatar : ''}
                        name={'avatar'} />
                </li>
                <li className="list-group-item">
                    <button onClick={() => updateSettingsThunk({token, nickname, avatar})} className="btn btn-primary float-right">–°–æ—Ö—Ä–∞–Ω–∏—Ç—å</button>
                </li>
                <li className="list-group-item">
                    <button onClick={() => deleteAccThunk({token})} className="btn btn-sm btn-danger">–£–¥–∞–ª–∏—Ç—å –∞–∫–∫–∞—É–Ω—Ç</button>
                </li>
            </ul>             
        </div>
    )
};

export default Settings;